import { Component, h, Element, Event, EventEmitter } from '@stencil/core';
import { product } from "../../../global/data/product";

@Component({
	tag: 'ks-product-count',
	styleUrl: 'product-count.css'
})
export class ProductCount {
    @Element() root: HTMLElement;

    input: HTMLInputElement;
    componentDidLoad() {
        this.input = this.root.querySelector("input");
    }
    
    @Event() countChange: EventEmitter<number>;
    CountChangeHandler() {
        this.countChange.emit(parseInt(this.input.value));
    }

    ClampLength() {
        if(this.input.value.length > 3)
            this.input.value = this.input.value.substr(0, 3);
    }

    ClampValue() {
        if(parseInt(this.input.value) > product.get("availability")) {
            this.input.value = product.get("availability").toString();
        }

        if(parseInt(this.input.value) < 1) {
            this.input.value = "1";
        }
    }

    Increment() {
        const value = parseInt(this.input.value) + 1;
        if(value <= product.get("availability")) {
            this.input.value = value.toString();
            this.CountChangeHandler();
        }
    }

    Decrement() {
        const value = parseInt(this.input.value) - 1;
        if(value >= 1) {
            this.input.value = value.toString();
            this.CountChangeHandler();
        }
    }

    Target(e: Event, func: (input: HTMLInputElement)=>void) {
        func(e.target as HTMLInputElement);
    }

	render() {
		return [
            <input type="number" min={1} max={product.get("availability")}
                value={1} aria-label="Ilość"
                onInput={()=>this.ClampLength()}
                onChange={()=>{this.ClampValue(); this.CountChangeHandler();}}
            />,
            <div class="spinner">
                <button onClick={()=>this.Increment()}>+</button>
                <button onClick={()=>this.Decrement()} aria-label="minus">-</button>
            </div>
		]
	}
}
